.note{
    padding: 25px;
    height: 280px;
    width: 260px;
    display: flex;
    flex-direction: column;
    background-color: brown;
    border-radius: 20px;
}

.note_text{
    flex: 1;
    resize: none;
    background-color: transparent;
    font-size: 1rem;
    line-height: 1.875rem;
    outline: none;
    border: none;
}

.note_footer{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    
}

.note_footer img{
    cursor: pointer;
    opacity: 0;
    transition: 300ms;
    width: 25px;
}

.note:hover .note_footer img{
    opacity: 1;
}